<template>
  <div v-if="event">
    <h3>{{ event.title }}</h3>
    <p>Join us {{ event.time }} {{ event.date }} @ {{ event.location }}</p>
    <p>Contact {{ event.organizer }} if you have any questions.</p>
  </div>
</template>

<script>
import EventService from '@/services/EventService.js'

export default {
  props: ['id'],
  data() {
    return {
      event: null,
    }
  },
  created() {
    EventService.getEvent(this.id)
      .then((response) => {
        this.event = response.data
      })
      .catch((error) => {
        console.log(error)
      })
  },
}
</script>
